  .dfooter__item {
    font-size: 9vw;
  }
}

.dfooter {
  min-width: 100vw;
  background: #232526;
  background: linear-gradient(to right, #414345, #232526);
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  border-top-left-radius: 7vh;
  border-top-right-radius: 7vh;
  position: absolute;
  bottom: 0;
  z-index: 7;
}
/* Sidebar Section */

.navbar__section-1 {
  height: 12.9vh;
  max-height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background-color: transparent;
}

.navbar__section__item,
.navbar__nav__section__item {
  font-size: 3.1vh;
  margin-left: 0.26vh;
  height: 5.15vh;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 12.9vh;
  min-width: 4vw;
  max-width: 11vw;
  flex-shrink: 0;
  background-color: #15161c;
  padding: 0.26vh;
  z-index: 400;
}

.navbar__nav {
  background: #15171c;
  width: 40vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  overflow: scroll;
  overflow-x: hidden;
  transition: all 0.4s 0.4s ease-in-out;
  transition-delay: 0ms;
  z-index: 401;
}

.navbar__nav__section {
  width: 100%;
}

/* Sub-Menu Section */

.submenu__item-1 {
